.q-default-hero__image {
	margin-bottom: 5em;
}
.q-default-hero__subheading {
	font-family: Caecilia, serif;
	font-size: 18px;
	line-height: 1.78;
	letter-spacing:-0.1px;
	color: #6A778B;
	font-weight: 400;
}
.q-alternate-hero__copy {
	margin-bottom: 0.67778em;
}
.cta {
	display: flex;
	color: #6A778B;
	line-height: 21px;
	font-family: Gotham, sans-serif;
	font-size: 15px;
}
.cta-call {
	margin-right: 30px;
}
.cta b {
	font-weight: 500;
}
.q-square {
	color: #222E41;
}
iframe {
	min-height: 2400px;
	border: 0;
	min-width: 914px;
	z-index: 10;
	position: relative;
	margin-top: 10px;
}
.q-square__content p {
	color: #FFF;
}
.q-grid__container {
	padding-top: 0;
}
.q-alternate-hero {
	padding-bottom: 60px;
}
@media (max-width: 1285px) {
	.q-alternate-hero__image-wrap {
		width: 36vw;
	}
}
@media (max-width: 1200px) {
	.cta {
		flex-wrap: wrap;
	}
	.cta-call p {
		margin-bottom: 0;
	}
}
@media (max-width: 990px) {
	.q-alternate-hero__content {
		width: 54%;
	}
}
@media (max-width: 880px) {
	.q-alternate-hero__image-wrap {
		width: 31vw;
	}
}
@media (max-width: 720px) {
	.q-alternate-hero__content {
		width: auto;
	}
	.q-alternate-hero__image-wrap {
		width: 70%;
	}
}
